@import "../../base/fn";

/*
z-index:
0: .weui-swiped-btn
1: .weui-cell_swiped .weui-cell__bd
2: .weui-cells.weui-cell1px
*/

.weui-cells {
    margin-top: @weuiCellsMarginTop;
    background-color: @weuiCellBg;
    line-height: @weuiCellLineHeight;
    font-size: @weuiCellFontSize; //cell23px

    overflow: hidden; //cellborderbeforeleftiecontaining blockcelloh

    // onepx
    position: relative;
    &:before {
        .setTopLine(@weuiCellBorderColor);
        z-index: 2;
    }
    &:after {
        .setBottomLine(@weuiCellBorderColor);
        z-index: 2;
    }
}

.weui-cells__title {
    margin-top: .77em; // 15px - 
    margin-bottom: .3em; // 8px - 
    padding-left: @weuiCellGapH;
    padding-right: @weuiCellGapH;
    color: @weuiTextColorGray;
    font-size: @weuiCellTipsFontSize;

    & + .weui-cells {
        margin-top: 0;
    }
}

.weui-cells__tips {
    margin-top: .3em; // 8px - 
    color: @weuiTextColorGray;
    padding-left: @weuiCellGapH;
    padding-right: @weuiCellGapH;
    font-size: @weuiCellTipsFontSize;
}

.weui-cell {
    padding: @weuiCellGapV @weuiCellGapH;
    position: relative; //cellsonepxbefore
    display: flex;
    align-items: center;
    &:before {
        .setTopLine(@weuiCellBorderColor);
        left: @weuiCellGapH;
        z-index: 2;
    }
    &:first-child {
        &:before {
            display: none;
        }
    }
}
.weui-cell_primary{
    align-items: flex-start;
}
.weui-cell__bd{
    flex: 1;
}
.weui-cell__ft {
    text-align: right;
    color: @weuiTextColorGray;
}